export const topOfTheList = "<topOfTheList>";
export function getPreview(items, targetItemId, isBefore, payload) {
    const maybeTargetItemId = targetItemId == topOfTheList ? undefined : targetItemId;
    return !payload.length
        ? items
        : items.reduce((acc, item) => {
            if (item.id === maybeTargetItemId && isBefore) {
                acc.push(...payload);
            }
            if (!payload.find((x) => x.id === item.id)) {
                acc.push(item);
            }
            if (item.id === maybeTargetItemId && !isBefore) {
                acc.push(...payload);
            }
            return acc;
        }, maybeTargetItemId ? [] : [...payload]);
}
